// Layout

section {
  display: flex;
  padding: 60px 0px;
  background: $background;
  border-bottom: 1px solid $section-border;
  color: $primary;
}

.section-inverse {
  background: $primary;
  color: $white;
  h1,h2,h3 {
    color: $white;
  }
}

.hays-card {
  background-color: $alt;
  color: $primary;
  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  flex-direction: column;
  transition: all .2s ease-in-out;
  max-width: 350px;
  &:hover {
    transform: scale(1.05);
    box-shadow: 0 1px 8px 2px (rgba(0, 0, 0, 0.5))
  }
  img {
    width: 100%;
  }
}

@media (max-width: 992px) {
  .card-title {
    font-size: 0.9em;
  }
  .card-text {
    font-size: 0.7em;
  }
  .hays-card {
    .button {
      font-size: 0.7em;
    }
  }
}


.hays-block {
  padding: 20px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
}

.hays-message {
  background-color: $primary;
  color: $white;
  text-align: center;
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  align-items: center;
  #wild {
    font-weight: 700;
    line-height: 1.1;
    font-size: 1.375em;
    text-transform: uppercase;
    letter-spacing: 0;
    color: $white;
  }
  p {
    font-size: 0.688em;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 7px;
    margin-bottom: 0;
  }
}

#places {
  h4 {
    font-weight: 300;
    color: $white;
    padding-bottom: $heading-space;
  }
  h3 {
    color: $primary;
  }
}
#hero {
  width: 100%;
  height: 100vh;
  background: url(../../img/state.png);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  padding-top: 200px;
  color: $alt;
  text-shadow: 1px 1px 5px (rgba(0, 0, 0, 0.5));
  border: none;
  .hero-header {
    color: $alt;
    text-shadow: 1px 1px 5px (rgba(0, 0, 0, 0.5));
  }
  h3 {
    color: $white;
  }
}

.hero-header {
  font-family: $hays-heading;
  font-size: 3em;
  font-weight: 700;
  color: $primary;
}

.subheader {
  font-family: $hays-heading;
  font-size: 2em;
  font-weight: 500;
}

.about-title {
  max-width: 760px;
  margin: 0 auto;
  padding: 0 15px;
  text-align: center;
  p {
    margin-bottom: 2em;
  }
}

#work-with-us {
  font-size: 2em;
  line-height: 1.1;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0;
}

#calendar-center {
  margin: 0 auto;
}

#events {
  text-align: center;
  h2 {
    padding-bottom: $spacing;
  }
  h4 {
    font-weight: 300;
    padding-bottom: $spacing;
  }
}

.googleCalendar {
  position: relative;
  height: 0;
  width: 100%;
  padding-bottom: 100%;
  iframe {
     position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.our-work {
  img {
    width: 100%;
    height: auto;
    box-shadow: 5px 5px 12px 2px (rgba(0, 0, 0, 0.5));
    transition: all .2s ease-in-out;
    &:hover {
      transform: scale(1.02);
    }
  }
  .work-text {
    display: flex;
    align-items: center;
    text-align: center;
    flex-direction: column;
    justify-content: center;
  }
  h2 {
    padding-bottom: 60px;
  }
}

.list-group-item {
    position: relative;
    display: block;
    padding: 10px 0;
    margin-bottom: -1px;
    background-color: transparent;
    border: none;
    color: $secondary;
    &:hover {
      color: $white;
    }
}

footer {
  padding-top: 40px;
  background-color: $primary;
  color: $white;
  h5 {
    color: $white;
  }
  a {
    color: $secondary;
    &:hover {
      color: $white;
    }
  }
}

.footer-list {
  ul {
    padding-left: 0;
  }
  li {
    list-style: none;
  }
}

#built-in-ktx {
  padding: 40px 0;
  background-color: $primary;
  display: flex;
  justify-content: center;
  border-top: 1px solid #e3e3e3;
  .btn-ktx {
    border: 3px solid $secondary;
    text-transform: uppercase;
    font-size: 0.7em;
    font-weight: 700;
    letter-spacing: 6px;
    padding: 10px 20px;
    background-color: transparent;
    border-radius: 3px;
    color: $secondary;
    &:hover {
      text-decoration: none;
    }
  }
}